import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue'
import OtherLogin from '../../components/home/other-login.vue'
export default {
	components: {
		uniNavBar,
		OtherLogin
	},
	data() {
		return {
			loading: false,
			isPhone: true,
			username: '',
			password: '',
			phone: '',
			checknum: '',
			codetime: 0 // 验证码倒计时
		}
	},
	computed: {
		typeText() {
			return this.isPhone ? '账号密码登录' : '验证码登录'
		},
		disabled() {
			if ((this.username && this.password) || (this.phone && this.checknum)) {
				return false
			} else {
				return true
			}
		}
	},
	methods: {
		// 初始化表单
		initForm() {
			this.username = ''
			this.password = ''
			this.phone = ''
			this.checknum = ''
		},
		// 返回
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		// 登录
		complete() {
			if (this.isPhone) {
				// 验证手机号合法性
				if (!this.checkPhone()) {
					uni.showToast({
						title: '请输入正确手机号',
						icon: 'none'
					})
					return 
				}
			}
		},
		forgetPassword() {
			
		},
		// 校验手机
		checkPhone() {
			let pattern = /^1[34578]\d{9}$/
			return pattern.test(this.phone)
		},
		// 获取验证码
		getYZM() {
			if (this.codetime > 0) {
				uni.showToast({
					title: '不能重复获取',
					icon: 'none'
				})
				return
			} else {
				// 验证手机号合法性					
				if (!this.checkPhone()) {
					uni.showToast({
						title: '请输入正确手机号',
						icon: 'none'
					})
					return 
				}
				this.codetime = 10
				let timer = setInterval(() => {
					this.codetime--
					if (this.codetime < 1) {
						clearInterval(timer)
						this.codetime = 0
					}
				}, 1000)
			}
		},
		toggleType() {
			this.initForm()
			this.isPhone = !this.isPhone
		}
	}
}
